<template>
    <v-container>
        <v-row>
            <v-col cols="12" md="5">
                <v-card>
                    <v-card-title class="text-h6">
                        {{ titulo_form }}
                    </v-card-title>

                    <v-divider></v-divider>

                    <v-alert
                        v-if="show_mensaje"
                        class="rounded-0 my-2"
                        dense
                        text
                        dismissible
                        :type="type_mensaje"
                    >
                        <small> {{ mensaje }} </small>
                    </v-alert>

                    <v-container>
                        <v-form
                            class="px-5"
                            ref="form"
                            v-model="valid"
                            lazy-validation
                        >
                            <input type="text" hidden v-model="form.id" />
                            <v-text-field
                                v-model="form.nombre"
                                :rules="[rules.required]"
                                label="Nombre"
                                required
                            ></v-text-field>

                            <v-switch
                                v-model="form.estado"
                                inset
                                dense
                                label="Habilitado"
                            ></v-switch>

                            <v-btn
                                :disabled="!valid"
                                color="primary"
                                class="mt-4"
                                block
                                :loading="loading_btn"
                                @click="Guardar"
                            >
                                Guardar
                            </v-btn>

                            <v-btn
                                v-if="isEdit"
                                color="red"
                                class="mt-3"
                                block
                                text
                                @click="reset_formulario"
                            >
                                <b> Cancelar</b>
                            </v-btn>
                        </v-form>
                    </v-container>
                </v-card>
            </v-col>
            <v-col cols="12" md="7">
                <v-card>
                    <v-data-table
                        :headers="headers"
                        :items="items"
                        sort-by="id"
                        class="elevation-1"
                    >
                        <template v-slot:top>
                            <v-toolbar flat>
                                <v-toolbar-title>Puestos</v-toolbar-title>

                                <v-dialog
                                    v-model="dialogDelete"
                                    max-width="300px"
                                >
                                    <v-card dark>
                                        <h5
                                            class="text-center text--white pa-5"
                                        >
                                            ¿Seguro de eliminar el registro?
                                        </h5>
                                        <v-divider></v-divider>
                                        <v-card-actions>
                                            <v-spacer></v-spacer>
                                            <v-btn
                                                color="gray darken-1"
                                                text
                                                @click="closeDelete"
                                                >Cancelar</v-btn
                                            >
                                            <v-btn
                                                :loading="loading_btn_eliminar"
                                                color="red darken-1"
                                                text
                                                @click="borrarItemConfirm"
                                                >ELIMINAR</v-btn
                                            >
                                            <v-spacer></v-spacer>
                                        </v-card-actions>
                                    </v-card>
                                </v-dialog>
                            </v-toolbar>
                        </template>
                        <template v-slot:item.actions="{ item }">
                            <v-icon small class="mr-2" @click="editItem(item)">
                                mdi-pencil
                            </v-icon>
                            <v-icon small @click="deleteItem(item)">
                                mdi-delete
                            </v-icon>
                        </template>
                        <template v-slot:no-data>
                            
                                Sin registros
                        </template>
                    </v-data-table>
                </v-card>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
import Layout from "@/Layouts/AdminLayout";
export default {
    metaInfo: { title: "Puesto" },
    layout: Layout,
    props: {
        puestos: Array,
    },
    data: () => ({
        rules: {
            required: (value) => !!value || "Requerido.",
            min: (v) => v.length >= 8 || "Min 6 caracteres",
            email: (v) => /.+@.+\..+/.test(v) || "Formato incorrecto",
        },
        loading_btn: false,
        loading_btn_eliminar: false,
        form: {
            id: false,
            nombre: "",
            estado: true,
        },
        valid: true,
        titulo_form: "Nuevo Registro",
        show_mensaje: false,
        type_mensaje: "success",
        mensaje: "",
        //tabla
        dialogDelete: false,
        headers: [
            {
                text: "ID",
                align: "start",
                sortable: true,
                value: "id",
            },
            { text: "Nombre", value: "nombre" },
            { text: "Estado", value: "estado" },

            { text: "Actions", value: "actions", sortable: false },
        ],

        items: [],
        editedIndex: -1,
        isEdit: false,
        editedItem: {
            id: false,
            nombre: "",
            estado: 0,
        },
    }),

    methods: {
        inicializar() {
            this.items = this.puestos;
        },
        validate_formulario() {
            return this.$refs.form.validate();
        },

        reset_formulario() {
            this.form.id = false;
            this.form.nombre = "";
            this.form.estado = true;
            this.isEdit = false;
            this.$refs.form.resetValidation();
        },

        async Guardar() {
            this.show_mensaje = false;

            try {
                this.loading_btn = true;
                if (this.validate_formulario()) {
                    let res = await axios.post("/puesto/guardar", this.form);

                    if (this.editedIndex > -1) {
                        Object.assign(
                            this.items[this.editedIndex],
                            res.data.datos
                        );
                    } else {
                        this.items.push(res.data.datos);
                    }

                    this.show_mensaje = true;
                    this.mensaje = res.data.mensaje;
                    this.loading_btn = false;
                    this.reset_formulario();
                }
            } catch (error) {
                this.type_mensaje = "error";
                this.mensaje = error.response.data.error;
                this.show_mensaje = true;
                this.loading_btn = false;
                this.reset_formulario();
            }
        },

        deleteItem(item) {
            this.editedIndex = this.items.indexOf(item);
            this.editedItem = Object.assign({}, item);
            this.dialogDelete = true;
        },

        async borrarItemConfirm() {
            this.loading_btn_eliminar = true;
            let res = await axios.delete(
                "/puesto/eliminar/" + this.editedItem.id
            );

            if (res.data.estado) {
                this.items.splice(this.editedIndex, 1);
            }

            this.closeDelete();
        },

        closeDelete() {
            this.dialogDelete = false;
            this.loading_btn_eliminar = false;
            this.$nextTick(() => {
                this.editedItem = Object.assign({}, this.defaultItem);
                this.editedIndex = -1;
            });
        },

        async editItem(item) {
            this.editedIndex = this.items.indexOf(item);
            this.form = Object.assign({}, item);
            this.titulo_form = "Editar Registro";
            this.isEdit = true;
        },
    },

     created() {
        this.inicializar();
    },

    
};
</script>
