<template>
    <v-app>
        <v-navigation-drawer app v-model="drawer" dark>
            <v-list-item color="primary">
                <v-list-item-content>
                    <v-row
                        class="fill-height ma-0"
                        align="center"
                        justify="center"
                    >

                     <img width="100%" src="/images/logomin.png" alt="">
                       
                    </v-row>
                </v-list-item-content>
            </v-list-item>
            <v-divider></v-divider>

            <v-card flat rounded="0" color="transparent">
                <v-list two-line class="py-0">
                    <v-list-item>
                        <v-list-item-avatar color="secondary">
                            <span class="white--text headline">
                                <small> AA </small>
                            </span>
                        </v-list-item-avatar>
                        <v-list-item-content>
                            <v-list-item-title>
                                Administrador
                            </v-list-item-title>
                            <v-list-item-subtitle>
                                Administrador</v-list-item-subtitle
                            >
                        </v-list-item-content>
                    </v-list-item>
                </v-list>
            </v-card>

            <v-divider />

            <menu-principal-component />

            <template v-slot:append>
                <v-divider></v-divider>
                <v-card-text class="text-center py-1">
                    <v-btn class="mx-4" icon link @click="salir">
                        <v-icon size="24px" color="blue-grey lighten-4">
                            mdi-power
                        </v-icon>
                    </v-btn>
                </v-card-text>
            </template>
        </v-navigation-drawer>
        <v-app-bar app color="white" scroll-off-screen>
            <v-app-bar-nav-icon
                @click.stop="drawer = !drawer"
            ></v-app-bar-nav-icon>

            <v-spacer/>

            <h5>CONTROL DE ASISTENCIA</h5>
            
        </v-app-bar>

        <v-main class="main_app">
            <transition name="component-fade" mode="out-in">
                <slot />
            </transition>
        </v-main>
        <v-footer app dark>
            <v-spacer />
            <h6>
               <!--by
                <a
                    href="https://www.linkedin.com/in/lino-puma-ticona-a76021202/"
                    target="_blank"
                    class="blue--text text-decoration-none"
                    >Lino Puma</a
                > -->
            </h6>
        </v-footer>
    </v-app>
</template>
<script>
import MenuPrincipalComponent from "@/components/MenuPrincipalComponent";
import axios from "axios";
export default {
    components: {
        MenuPrincipalComponent,
    },
    data: () => ({
        drawer: true,
        attrs: {
            class: "mb-1",
            boilerplate: true,
            elevation: 0,
            loading: false,
        },
        user: null,
        closeOnContentClick: true,
    }),
    methods: {
        async GetCurrentUser() {
            try {
                let res = await axios.get("/currentUser");
                this.user = res.data.datos;
                if (res.data.datos == null) {
                    this.$inertia.replace("/login");
                }
            } catch (error) {
                console.log("no autenticado");
            }
        },
        salir() {
            this.$inertia.post("/logout");
        },
    },
    async created() {
        //await this.GetCurrentUser();
    },
};
</script>
<style scoped>
.main_app {
    background: #eeeeee;
}
.component-fade-enter-active,
.component-fade-leave-active {
    transition: opacity 0.3s ease;
}
.component-fade-enter, .component-fade-leave-to
/* .component-fade-leave-active below version 2.1.8 */ {
    opacity: 0;
}
</style>
